header {
  height: 94px;
  margin-bottom: 30px;
  background: #4277b7 url('http://i.imgur.com/Pm5hf.png') repeat-x;
}
header h1 {
  text-indent: -9999px;
  background: url('http://i.imgur.com/t5AI4.png') no-repeat center;
  width: 500px;
  height: 94px;
  margin: 0;
  padding: 0;
}
body {
  background-color: #FFF;
  color: #2c3e50;
  font-family: 'Avenir New', 'Muli';
}
input:focus,
textarea:focus {
  border-color: #c43825 !important;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px #c43825 !important;
}
.close {
  font-size: 35px;
}
@media (max-width: 767px) {
  header {
    margin-left: -20px;
    margin-right: -20px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 540px) {
  header h1 {
    background: url('http://i.imgur.com/8LfcwUs.png') no-repeat center;
    width: 100%;
  }
}
