.top {
  margin-top: 10px;
}
.bottom {
  margin-bottom: 10px;
}
.corner {
  border-radius: 0;
}
.btn-danger {
  background-image: linear-gradient(to top, #c43825, #db422c);
  font-weight: bold;
  color: #ffffff;
}
.contact-error {
  color: #C0392B;
}
.bold {
  font-weight: bold;
}
.red {
  color: #c43825;
}
.navy {
  color: #34495e;
}
.gray {
  color: #889596;
}
.white {
  color: #ffffff;
}
.desaturate {
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  filter: grayscale(100%);
}
.red-hue {
  -webkit-filter: sepia(1) saturate(1) hue-rotate(300deg);
  -moz-filter: sepia(1) saturate(1) hue-rotate(300deg);
  filter: sepia(1) saturate(1) hue-rotate(300deg);
}
.spinner {
  width: 45px;
  height: 45px;
}
/*****************************************************************************/
/*****************************************************************************/
.modal-header {
  background-color: #c43825;
}
/*****************************************************************************/
/*****************************************************************************/
.frontpage-navbar {
  min-height: 70px;
  background-color: #ffffff;
  margin-bottom: 0;
}
.frontpage-navbar .navbar-brand {
  height: 60px;
  width: 160px;
  margin-top: 5px;
  padding: 0;
}
.frontpage-navbar img {
  width: 100%;
}
.frontpage-navbar li {
  height: 70px;
  font-size: 20px;
  padding-top: 15px;
}
.frontpage-navbar li:last-child {
  padding-top: 0px;
}
/*****************************************************************************/
/*****************************************************************************/
.side-nav {
  position: fixed;
  top: 130px;
  font-size: 18px;
}
.side-nav li {
  cursor: pointer;
}
.side-nav li.active a {
  background-color: #c43825;
}
.side-nav li.active a:hover {
  background-color: #E74C3C;
}
.side-nav li a {
  color: #34495e;
}
.side-nav li a:hover {
  background-color: #f8f9f8;
}
