/*****************************************************************************/
.home-slide-panel {
  width: 100%;
  background-size: 100% 100%;
  height: 768px;
}
.home-slide-panel.panel-1 {
  height: 300px;
  background-image: url('../img/pitchPhoto.jpg');
  padding-top: 5%;
}
.home-slide-panel.panel-1 .pitch-question {
  margin-top: 5%;
}
.home-slide-panel.panel-1 .pitch-question div {
  display: none;
}
.home-slide-panel.panel-1 .pitch-features {
  margin-top: 5%;
}
.home-slide-panel.panel-1 .pitch-features div {
  display: none;
}
.home-slide-panel.panel-1 .pitch-logo {
  display: none;
}
.home-slide-panel.panel-1 .pitch-logo img {
  height: 80px;
  margin-top: 10%;
}
.home-slide-panel.panel-1 .slide-content {
  display: block;
  margin-bottom: 5px;
  padding: 10px;
  background-color: #ffffff;
  font-size: 18px;
  font-family: inherit;
  font-weight: 500;
  line-height: 1.1;
  color: inherit;
}
@media (min-width: 768px) {
  .home-slide-panel.panel-1 {
    height: 500px;
  }
  .home-slide-panel.panel-1 .slide-content {
    font-size: 30px;
  }
  .home-slide-panel.panel-1 .pitch-logo img {
    height: auto;
    margin-top: 0;
  }
}
.home-slide-panel.panel-2 {
  background-color: #ffffff;
  padding-top: 10%;
  margin-bottom: 10%;
}
.home-slide-panel.panel-2 h1 {
  line-height: 1.3;
}
.home-slide-panel.panel-2 h3 {
  line-height: 1.3;
  font-weight: bold;
}
.home-slide-panel.panel-2 .tablet {
  width: 200px;
  height: 153px;
  margin-top: 10%;
  padding: 16px 17px 14px 19px;
  background-image: url('../img/tablet.png');
  background-repeat: no-repeat;
  background-size: 200px 153px;
}
.home-slide-panel.panel-2 img {
  width: 180px;
}
@media (min-width: 768px) {
  .home-slide-panel.panel-2 {
    margin-bottom: 0;
  }
  .home-slide-panel.panel-2 .tablet {
    width: 465px;
    height: 356px;
    margin-top: 0;
    padding: 36px 40px 35px 45px;
    background-size: 465px 356px;
  }
  .home-slide-panel.panel-2 img {
    width: 380px;
  }
}
.home-slide-panel.panel-3 {
  height: 1800px;
  background-color: #f8f9f8;
  padding-top: 5%;
}
.home-slide-panel.panel-3 h1 {
  line-height: 1.3;
}
.home-slide-panel.panel-3 h4 {
  margin-top: 2%;
  margin-bottom: 4%;
  line-height: 1.3;
}
.home-slide-panel.panel-3 ul {
  margin-top: 4%;
  list-style: none;
  padding-left: 0;
  font-size: 20px;
}
.home-slide-panel.panel-3 ul li {
  margin-bottom: 1%;
}
.home-slide-panel.panel-3 img {
  width: 100px;
}
.home-slide-panel.panel-3 .feature {
  height: 340px;
  margin-top: 10%;
  text-align: center;
}
.home-slide-panel.panel-3 .feature-nav {
  margin-top: 3%;
  cursor: pointer;
}
.home-slide-panel.panel-3 .feature-nav img {
  width: 82px;
  height: 82px;
}
@media (min-width: 768px) {
  .home-slide-panel.panel-3 {
    height: 768px;
  }
  .home-slide-panel.panel-3 .feature {
    margin-top: 0;
    text-align: left;
  }
  .home-slide-panel.panel-3 img {
    width: auto;
    height: 280px;
  }
}
.home-slide-panel.panel-4 {
  background-color: #ffffff;
  padding-top: 5%;
  height: 768px;
}
.home-slide-panel.panel-4 h1 {
  margin-top: 5%;
}
@media (min-width: 768px) {
  .home-slide-panel.panel-4 {
    height: 700px;
  }
  .home-slide-panel.panel-4 .feature {
    margin-top: 0;
    text-align: left;
  }
  .home-slide-panel.panel-4 img {
    width: auto;
    height: 280px;
  }
}
.home-slide-panel.panel-5 {
  background-image: url(data:image/jpeg;base64,);
  background-size: 100% 100%;
  height: 300px;
  padding-top: 3%;
}
.home-slide-panel.panel-5 h1 {
  color: #ffffff;
}
.home-slide-panel.panel-5 button {
  margin-top: 5%;
  padding: 29px;
  font-size: 15px;
}
@media (min-width: 768px) {
  .home-slide-panel.panel-5 button {
    font-size: 25px;
  }
}
.home-slide-panel.panel-6 {
  background-color: #ffffff;
  height: auto;
  padding-top: 3%;
  padding-bottom: 3%;
  margin-bottom: 3%;
}
.home-slide-panel.panel-6 h5 {
  line-height: 2;
}
.home-slide-panel.panel-6 img {
  width: 100%;
  height: 100%;
  max-width: 45px;
}
.home-slide-panel.panel-6 img:hover {
  -webkit-filter: none;
  -moz-filter: none;
  filter: none;
}
@media (min-width: 768px) {
  .home-slide-panel.panel-6 .divider {
    border-right: 1px solid #000;
  }
}
